import React from 'react'

const Course = (props) => (
      <li className="CoursesList-item">
              <a className="CoursesList-link" href={props.url} target="_blank">
                  <figure>
                      <img src={props.poster} alt={props.name}/>
                      <figcaption>
                        <h2>{props.name}</h2>
                      </figcaption>
                  </figure>
              </a>
              <section>
                  <p><i className="fa fa-graduation-cap"></i>
                  {props.teacher}
                  </p>
                  <p><i className="fa fa-calendar-o"></i>
                  {props.date}
                  </p>
                  <p><i className="fa fa-dollar"></i>
                  {props.amount}
                  </p>
                  <p><i className="fa fa-dollar"></i>
                    {
                    props.categories.map((cat,index,arr)=>(
                      (index === arr.length -1)
                      ?
                        cat 
                      : `${cat},`

                    ))
                    
                    }
                  </p>
              </section>
              
          </li>
)

export default Course